/**
 * 
 *
 *  VueUse  useClipboard toastr 
 *
 * @example
 * ```vue
 * <script setup>
 * const { copyWithToast, copied } = useCopyWithFeedback();
 *
 * const handleCopy = async () => {
 *   const success = await copyWithToast('');
 *   if (success) {
 *     console.log('');
 *   }
 * };
 * </script>
 * ```
 */
export function useCopyWithFeedback() {
    const { copy, copied, isSupported } = useClipboard();
    /**
     *  toast 
     */
    const copyWithToast = async (text, successMessage = '') => {
        // 
        const { toast } = await import('./useToast');
        if (!isSupported.value) {
            console.warn('[ACU]  API ');
            toast.warning('');
            return false;
        }
        try {
            await copy(text);
            if (copied.value) {
                toast.success(successMessage);
                return true;
            }
            return false;
        }
        catch (error) {
            console.error('[ACU] :', error);
            toast.error('');
            return false;
        }
    };
    return {
        copyWithToast,
        copied,
        isSupported,
        /**  */
        copy,
    };
}
