/**
 * 
 *
 * 
 *
 * @example
 * ```vue
 * <script setup>
 * const data = ref({ name: 'test' });
 *
 * useDebouncedSave(
 *   () => data.value,
 *   async (data) => {
 *     await saveToServer(data);
 *   },
 *   {
 *     delay: 500,
 *     onSuccess: () => console.log(''),
 *     onError: (err) => console.error('', err)
 *   }
 * );
 * </script>
 * ```
 */
export function useDebouncedSave(source, saveFn, options = {}) {
    const { delay = 500, deep = true, onSuccess, onError } = options;
    const isSaving = ref(false);
    const lastError = ref(null);
    watchDebounced(source, async (newValue) => {
        isSaving.value = true;
        lastError.value = null;
        try {
            await saveFn(newValue);
            onSuccess?.();
        }
        catch (error) {
            lastError.value = error;
            console.error('[ACU] :', error);
            onError?.(error);
        }
        finally {
            isSaving.value = false;
        }
    }, { debounce: delay, deep });
    return {
        /**  */
        isSaving,
        /**  */
        lastError,
    };
}
