/**  - AI */
export type ChangeType = 'manual' | 'ai' | null;

export interface ACUConfig {
  /**  ID */
  theme: string;
  /**  ID */
  fontFamily: string;
  /**  */
  cardWidth: number;
  /**  */
  fontSize: number;
  /**  */
  itemsPerPage: number;
  /**  */
  highlightNew: boolean;
  /**  key () */
  highlightColor: string;
  /**  key */
  highlightManualColor: string;
  /** AI key */
  highlightAiColor: string;
  /**  */
  customTitleColor: boolean;
  /**  key */
  titleColor: string;
  /**  (hex) */
  customHighlightManualHex?: string;
  /**  AI  (hex) */
  customHighlightAiHex?: string;
  /**  (hex) */
  customTitleHex?: string;
  /**  */
  layout: 'vertical' | 'horizontal';
  /**  */
  limitLongText: boolean;
  /**  */
  showDashboard: boolean;
  /**  */
  showPagination: boolean;
  /**  */
  lockPanel: boolean;
  /**  */
  purgeConfirmation: boolean;
  /**  (0 ) */
  gridColumns: number;
  /**  */
  autoSave?: boolean;
  /**  () */
  autoSaveDelay?: number;
  /**  ID  */
  visibleButtons: string[];
  /**  */
  buttonOrder: string[];
  /**  */
  buttonGroups: ButtonGroup[];
  /**  */
  longPressDirectExec: boolean;
  /** Swipe  */
  clearTableOnSwipe?: boolean;
  /** TabTab */
  collapseTabBar?: boolean;
}

/**  */
export interface NavButtonConfig {
  /**  ID */
  id: string;
  /**  class (FontAwesome) */
  icon: string;
  /**  */
  label: string;
  /**  () */
  hidden?: boolean;
}

/**
 * 
 * 
 */
export interface ButtonGroup {
  /**  ID */
  primaryId: string;
  /**  ID */
  secondaryId: string | null;
}

/**
 * Tab  Tab 
 */
export interface TabConfigEntry {
  /** Tab ID */
  id: string;
  /** Tab  */
  name: string;
  /** Tab  () */
  icon?: string;
  /** Tab  */
  type: 'normal' | 'dashboard' | 'options' | 'special';
  /**  */
  visible: boolean;
  /**  */
  order: number;
}

/** Tab  */
export interface TabConfigItem {
  /** Tab ID */
  id: string;
  /** Tab  */
  name: string;
  /** Tab  */
  icon: string;
  /**  */
  visible: boolean;
  /**  */
  order: number;
}

