/**  */
export interface RawDatabaseData {
  [sheetId: string]: {
    name?: string;
    content?: (string | number)[][];
  };
}

/**  */
export interface TableRow {
  /**  */
  index: number;
  /**  */
  key: string;
  /**  */
  cells: TableCell[];
  /**  */
  changed?: boolean;
  /**  */
  deleting?: boolean;
}

/**  */
export interface TableCell {
  /**  */
  colIndex: number;
  /** / */
  key: string;
  /**  */
  value: string | number;
  /**  */
  changed?: boolean;
}

/**  */
export interface ProcessedTable {
  /**  ID */
  id: string;
  /**  */
  name: string;
  /**  */
  headers: string[];
  /**  */
  rows: TableRow[];
}

