/**  */
export type FloatingBallAnimation = 'ripple' | 'arc';

/**  */
export type FloatingBallIconType = 'icon' | 'emoji' | 'image';

/**
 * 
 * 
 */
export interface FloatingBallAppearance {
  /** : FontAwesome / Emoji /  */
  type: FloatingBallIconType;
  /**
   * :
   * - type='icon': FA class ( 'fa-layer-group')
   * - type='emoji': Emoji ( '')
   * - type='image': Base64 
   */
  content: string;
  /**  (40-100px) */
  size: number;
  /** AI */
  notifyAnimation: FloatingBallAnimation;
  /**  (hex  '#90cdf4') */
  borderColor: string;
  /**  (0-100) */
  borderOpacity: number;
  /**  (hex  '#2b2b2b') */
  bgColor: string;
  /**  (0-100) */
  bgOpacity: number;
}

