/**
 *  Base64
 * @param file 
 * @returns Base64  ( data:image/xxx;base64, )
 */
export function fileToBase64(file: File): Promise<string> {
  return new Promise((resolve, reject) => {
    const reader = new FileReader();
    reader.readAsDataURL(file);
    reader.onload = () => resolve(reader.result as string);
    reader.onerror = reject;
  });
}

/**
 * 
 * @param base64  Base64 
 * @param maxWidth  ( 100)
 * @param quality  0-1 ( 0.8)
 * @returns  Base64 
 */
export function compressImage(base64: string, maxWidth = 100, quality = 0.8): Promise<string> {
  return new Promise((resolve, reject) => {
    const img = new Image();
    img.onload = () => {
      try {
        const canvas = document.createElement('canvas');
        const scale = Math.min(1, maxWidth / Math.max(img.width, img.height));
        canvas.width = img.width * scale;
        canvas.height = img.height * scale;

        const ctx = canvas.getContext('2d');
        if (!ctx) {
          reject(new Error(' canvas context'));
          return;
        }

        ctx.drawImage(img, 0, 0, canvas.width, canvas.height);
        resolve(canvas.toDataURL('image/jpeg', quality));
      } catch (e) {
        reject(e);
      }
    };
    img.onerror = () => reject(new Error(''));
    img.src = base64;
  });
}

/**
 *  Base64 
 * @param base64 Base64 
 * @returns 
 */
export function getBase64Size(base64: string): number {
  //  data:image/xxx;base64, 
  const base64Data = base64.split(',')[1] || base64;
  // Base64  4/3
  return Math.ceil(base64Data.length * 0.75);
}

/**
 * 
 * @param bytes 
 * @returns  ( "50 KB")
 */
export function formatFileSize(bytes: number): string {
  if (bytes < 1024) return `${bytes} B`;
  if (bytes < 1024 * 1024) return `${(bytes / 1024).toFixed(1)} KB`;
  return `${(bytes / (1024 * 1024)).toFixed(1)} MB`;
}

/**
 * 
 * @param options.maxWidth  ( 100)
 * @param options.quality  ( 0.8)
 * @param options.maxSizeKB  KB ( 50)
 * @returns Base64  null
 */
export function uploadImage(options?: {
  maxWidth?: number;
  quality?: number;
  maxSizeKB?: number;
}): Promise<string | null> {
  const { maxWidth = 100, quality = 0.8, maxSizeKB = 50 } = options || {};

  return new Promise(resolve => {
    const input = document.createElement('input');
    input.type = 'file';
    input.accept = 'image/*';

    input.onchange = async () => {
      const file = input.files?.[0];
      if (!file) {
        resolve(null);
        return;
      }

      try {
        let base64 = await fileToBase64(file);

        // 
        base64 = await compressImage(base64, maxWidth, quality);

        // 
        const sizeKB = getBase64Size(base64) / 1024;
        if (sizeKB > maxSizeKB) {
          // 
          base64 = await compressImage(base64, maxWidth * 0.7, quality * 0.7);
          const newSizeKB = getBase64Size(base64) / 1024;
          if (newSizeKB > maxSizeKB) {
            console.warn(`[ACU]  ${maxSizeKB}KB (${newSizeKB.toFixed(1)}KB)`);
          }
        }

        resolve(base64);
      } catch (e) {
        console.error('[ACU] :', e);
        resolve(null);
      }
    };

    input.oncancel = () => resolve(null);
    input.click();
  });
}

/**
 *  hex  rgba
 * @param hex Hex  ( '#ff0000')
 * @param opacity  0-100
 * @returns rgba 
 */
export function hexToRgba(hex: string, opacity: number): string {
  const result = /^#?([a-f\d]{2})([a-f\d]{2})([a-f\d]{2})$/i.exec(hex);
  if (!result) return hex;
  const r = parseInt(result[1], 16);
  const g = parseInt(result[2], 16);
  const b = parseInt(result[3], 16);
  return `rgba(${r}, ${g}, ${b}, ${opacity / 100})`;
}

/**
 *  rgba  hex  opacity
 * @param rgba rgba 
 * @returns { hex, opacity }
 */
export function rgbaToHex(rgba: string): { hex: string; opacity: number } {
  const match = rgba.match(/rgba?\((\d+),\s*(\d+),\s*(\d+)(?:,\s*([\d.]+))?\)/);
  if (!match) return { hex: rgba, opacity: 100 };

  const r = parseInt(match[1]).toString(16).padStart(2, '0');
  const g = parseInt(match[2]).toString(16).padStart(2, '0');
  const b = parseInt(match[3]).toString(16).padStart(2, '0');
  const opacity = match[4] ? Math.round(parseFloat(match[4]) * 100) : 100;

  return { hex: `#${r}${g}${b}`, opacity };
}
