/**
 * UI  Store
 *  isCollapsed, isPinned, currentPage, currentSearchTerm, globalScrollTop, isEditingOrder 
 */
import { useStorage } from '@vueuse/core';
import { useDataStore } from './useDataStore';
/**  () */
export const STORAGE_KEYS = {
    UI_COLLAPSE: 'acu_ui_collapse_state',
    PIN: 'acu_pin_state',
    ACTIVE_TAB: 'acu_active_tab',
    TABLE_ORDER: 'acu_table_order',
    TABLE_HEIGHTS: 'acu_table_heights',
    TABLE_STYLES: 'acu_table_styles',
    REVERSE_TABLES: 'acu_reverse_tables',
    BALL_POSITION: 'acu_float_ball_pos',
    LAYOUT: 'acu_layout',
    VISIBLE_TABS: 'acu_visible_tabs',
    TAB_ORDER: 'acu_tab_order',
};
/**  Tab  */
export const TAB_DASHBOARD = 'acu_tab_dashboard_home';
export const TAB_OPTIONS = 'acu_tab_options_panel';
export const TAB_RELATIONSHIP_GRAPH = 'acu_tab_relationship_graph';
export const useUIStore = defineStore('acu-ui', () => {
    // ============================================================
    //  -  useStorage  localStorage
    // ============================================================
    /**  -  isCollapsed */
    const isCollapsed = useStorage(STORAGE_KEYS.UI_COLLAPSE, false);
    /**  -  isPinned */
    const isPinned = useStorage(STORAGE_KEYS.PIN, false);
    /**  Tab -  getActiveTabState/saveActiveTabState */
    const activeTab = useStorage(STORAGE_KEYS.ACTIVE_TAB, null);
    /**  -  getSavedTableOrder/saveTableOrder */
    const tableOrder = useStorage(STORAGE_KEYS.TABLE_ORDER, []);
    /**  -  getTableHeights/saveTableHeights */
    const tableHeights = useStorage(STORAGE_KEYS.TABLE_HEIGHTS, {});
    /**  -  getTableStyles/saveTableStyles */
    const tableStyles = useStorage(STORAGE_KEYS.TABLE_STYLES, {});
    /**  -  getReverseOrderTables/saveReverseOrderTables */
    const reverseTables = useStorage(STORAGE_KEYS.REVERSE_TABLES, []);
    /**  */
    const ballPosition = useStorage(STORAGE_KEYS.BALL_POSITION, {
        x: 20,
        y: typeof window !== 'undefined' ? window.innerHeight - 150 : 500,
    });
    /**  - (horizontal)(vertical) */
    const layout = useStorage(STORAGE_KEYS.LAYOUT, 'horizontal');
    /**  Tab ID  -  */
    const visibleTabs = useStorage(STORAGE_KEYS.VISIBLE_TABS, []);
    /** Tab  -  */
    const tabOrder = useStorage(STORAGE_KEYS.TAB_ORDER, []);
    // ============================================================
    //  - 
    // ============================================================
    /**  -  currentPage */
    const currentPage = ref(1);
    /**  -  currentSearchTerm */
    const searchTerm = ref('');
    /**  -  globalScrollTop */
    const scrollTop = ref(0);
    /**  -  isEditingOrder */
    const isEditingOrder = ref(false);
    /**  -  isInitialized */
    const isInitialized = ref(false);
    /**  () */
    const isContentHidden = ref(false);
    /**  */
    const isMobile = ref(false);
    /**  */
    const isLockEditMode = ref(false);
    /**  */
    const showLockedOnly = ref(false);
    // ============================================================
    // Getters
    // ============================================================
    /**  */
    const isPanelVisible = computed(() => !isCollapsed.value && activeTab.value !== null);
    /**  */
    const isDashboardActive = computed(() => activeTab.value === TAB_DASHBOARD);
    /**  */
    const isOptionsActive = computed(() => activeTab.value === TAB_OPTIONS);
    // ============================================================
    // Actions
    // ============================================================
    /**
     * 
     */
    function toggleCollapse() {
        isCollapsed.value = !isCollapsed.value;
    }
    /**
     * 
     */
    function togglePin() {
        isPinned.value = !isPinned.value;
    }
    /**
     *  Tab
     * @param tabName Tab 
     */
    function setActiveTab(tabName) {
        activeTab.value = tabName;
        //  tab 
        currentPage.value = 1;
        searchTerm.value = '';
    }
    /**
     * 
     * @param order 
     */
    function setTableOrder(order) {
        tableOrder.value = order;
    }
    /**
     * 
     * @param tableName 
     * @param height 
     */
    function setTableHeight(tableName, height) {
        tableHeights.value = { ...tableHeights.value, [tableName]: height };
    }
    /**
     *  ()
     * @param tableName 
     */
    function resetTableHeight(tableName) {
        const heights = { ...tableHeights.value };
        delete heights[tableName];
        tableHeights.value = heights;
    }
    /**
     * 
     * @param tableName 
     */
    function getTableHeight(tableName) {
        return tableHeights.value[tableName];
    }
    /**
     * 
     * @param tableName 
     * @param style 
     */
    function setTableStyle(tableName, style) {
        tableStyles.value = { ...tableStyles.value, [tableName]: style };
    }
    /**
     * 
     * @param tableName 
     */
    function getTableStyle(tableName) {
        return tableStyles.value[tableName] || 'list';
    }
    /**
     * 
     * @param tableName 
     */
    function toggleTableReverse(tableName) {
        const current = [...reverseTables.value];
        const index = current.indexOf(tableName);
        if (index > -1) {
            current.splice(index, 1);
        }
        else {
            current.push(tableName);
        }
        reverseTables.value = current;
    }
    /**
     * 
     * @param tableName 
     */
    function isTableReversed(tableName) {
        return reverseTables.value.includes(tableName);
    }
    /**
     * 
     * @param page 
     */
    function setCurrentPage(page) {
        currentPage.value = Math.max(1, page);
    }
    /**
     * 
     * @param term 
     */
    function setSearchTerm(term) {
        searchTerm.value = term.toLowerCase();
        // 
        currentPage.value = 1;
    }
    /**
     * 
     */
    const currentSearchTerm = computed(() => searchTerm.value);
    /**
     * 
     * @param top 
     */
    function saveScrollTop(top) {
        scrollTop.value = top;
    }
    /**
     * 
     */
    function enterEditingOrder() {
        isEditingOrder.value = true;
    }
    /**
     * 
     */
    function exitEditingOrder() {
        isEditingOrder.value = false;
    }
    /**
     * 
     * @param status 
     */
    function setEditingOrder(status) {
        isEditingOrder.value = status;
    }
    /**
     * 
     * @param status 
     */
    function setMobile(status) {
        isMobile.value = status;
    }
    /**
     * 
     * @param newLayout 
     */
    function setLayout(newLayout) {
        layout.value = newLayout;
    }
    /**
     * 
     */
    function toggleLayout() {
        layout.value = layout.value === 'horizontal' ? 'vertical' : 'horizontal';
    }
    /**
     * 
     */
    const isHorizontalLayout = computed(() => layout.value === 'horizontal');
    /**
     *  ()
     */
    function closePanel() {
        isCollapsed.value = true; // 
        isContentHidden.value = false; // 
    }
    /**
     * 
     *  Tab
     */
    function openPanel() {
        isContentHidden.value = false;
        isCollapsed.value = false;
        //  TabisPanelVisible  activeTab !== null
        if (activeTab.value === null) {
            activeTab.value = TAB_DASHBOARD;
        }
    }
    /**
     * 
     * @param status 
     */
    function setInitialized(status) {
        isInitialized.value = status;
    }
    /**
     * 
     * @param position 
     */
    function saveBallPosition(position) {
        ballPosition.value = position;
    }
    /**
     * 
     */
    function getBallPosition() {
        return ballPosition.value;
    }
    /**
     * 
     *  iframe 
     */
    function resetBallPosition() {
        const parentHeight = typeof window !== 'undefined' ? (window.parent?.innerHeight ?? window.innerHeight) : 500;
        ballPosition.value = {
            x: 20,
            y: Math.max(100, parentHeight - 150), // 
        };
    }
    /**
     * 
     * @param force 
     */
    function autoFitPanelHeight(force = false) {
        const { $ } = window.jQuery ? { $: window.jQuery } : { $: window.parent.$ };
        if (!$)
            return;
        const $panel = $(window.parent.document).find('#acu-data-area');
        if (!$panel.length)
            return;
        // 
        const currentTab = activeTab.value;
        if (!force && currentTab && tableHeights.value[currentTab]) {
            return;
        }
        // 
        // 
        // 
        setTimeout(() => {
            const contentHeight = $panel[0].scrollHeight;
            const winHeight = $(window.parent).height() || 800;
            const maxHeight = winHeight - 200;
            let newHeight = Math.min(contentHeight + 20, maxHeight);
            if (newHeight < 200)
                newHeight = 200;
            $panel.css('height', newHeight + 'px');
        }, 100);
    }
    // ============================================================
    // Tab  Actions
    // ============================================================
    /**
     *  Tab
     * @param tabs Tab ID 
     */
    function setVisibleTabs(tabs) {
        visibleTabs.value = tabs;
    }
    /**
     *  Tab 
     * @param tabId Tab ID
     */
    function isTabVisible(tabId) {
        // 
        if (visibleTabs.value.length === 0)
            return true;
        return visibleTabs.value.includes(tabId);
    }
    /**
     *  Tab 
     * @param tabId Tab ID
     */
    function toggleTabVisibility(tabId) {
        const current = [...visibleTabs.value];
        const index = current.indexOf(tabId);
        if (index > -1) {
            current.splice(index, 1);
        }
        else {
            current.push(tabId);
        }
        visibleTabs.value = current;
    }
    /**
     *  Tab 
     */
    function resetVisibleTabs() {
        visibleTabs.value = [];
    }
    /**
     *  Tab 
     * @param order Tab ID 
     */
    function setTabOrder(order) {
        tabOrder.value = order;
    }
    /**
     *  Tab ID 
     * @param allTabIds  Tab ID 
     * @returns  Tab ID 
     */
    function getSortedTabIds(allTabIds) {
        if (!tabOrder.value || tabOrder.value.length === 0) {
            return allTabIds;
        }
        //  tabOrder  tabOrder 
        const orderedIds = tabOrder.value.filter(id => allTabIds.includes(id));
        const unorderedIds = allTabIds.filter(id => !tabOrder.value.includes(id));
        return [...orderedIds, ...unorderedIds];
    }
    /**
     *  Tab 
     * @param tabId  Tab ID
     * @param newIndex 
     * @param allTabIds  Tab ID 
     */
    function moveTab(tabId, newIndex, allTabIds) {
        const currentOrder = getSortedTabIds(allTabIds);
        const currentIndex = currentOrder.indexOf(tabId);
        if (currentIndex === -1)
            return;
        const newOrder = [...currentOrder];
        newOrder.splice(currentIndex, 1);
        newOrder.splice(newIndex, 0, tabId);
        tabOrder.value = newOrder;
    }
    /**
     *  Tab 
     * @param tabId Tab ID
     */
    function showTab(tabId) {
        if (!visibleTabs.value.includes(tabId)) {
            visibleTabs.value = [...visibleTabs.value, tabId];
        }
    }
    /**
     *  Tab 
     * @param tabId Tab ID
     */
    function hideTab(tabId) {
        visibleTabs.value = visibleTabs.value.filter(id => id !== tabId);
    }
    /**
     *  Tab 
     */
    function resetTabConfig() {
        visibleTabs.value = [];
        tabOrder.value = [];
    }
    /**
     * 
     *  visibleTabs 
     * @param allTableIds  ID 
     * @returns  ID 
     */
    function syncNewTablesToVisibleTabs(allTableIds) {
        //  visibleTabs 
        if (visibleTabs.value.length === 0) {
            return [];
        }
        //  visibleTabs 
        const newTabIds = allTableIds.filter(id => !visibleTabs.value.includes(id));
        // 
        if (newTabIds.length > 0) {
            visibleTabs.value = [...visibleTabs.value, ...newTabIds];
            console.info(`[ACU]  ${newTabIds.length} :`, newTabIds);
        }
        return newTabIds;
    }
    // ============================================================
    //  Actions
    // ============================================================
    /**
     * 
     * ( updateSaveBtnState)
     */
    function updateSaveBtnState() {
        const { $ } = window.jQuery ? { $: window.jQuery } : { $: window.parent.$ };
        if (!$)
            return;
        const dataStore = useDataStore();
        const hasChanges = dataStore.hasUnsavedChanges;
        const $btn = $(window.parent.document).find('#acu-btn-save-global');
        if ($btn.length) {
            if (hasChanges) {
                $btn.addClass('acu-has-changes');
                $btn.find('i').addClass('fa-beat-fade');
                $btn.css('color', '#e67e22');
            }
            else {
                $btn.removeClass('acu-has-changes');
                $btn.find('i').removeClass('fa-beat-fade');
                $btn.css('color', '');
            }
        }
    }
    return {
        // 
        isCollapsed,
        isPinned,
        activeTab,
        tableOrder,
        tableHeights,
        tableStyles,
        reverseTables,
        ballPosition,
        layout,
        visibleTabs,
        // 
        currentPage,
        searchTerm,
        currentSearchTerm,
        scrollTop,
        isEditingOrder,
        isInitialized,
        isContentHidden,
        isMobile,
        isLockEditMode,
        showLockedOnly,
        // Getters
        isPanelVisible,
        isDashboardActive,
        isOptionsActive,
        reverseOrderTables: reverseTables,
        isHorizontalLayout,
        // Actions
        toggleCollapse,
        togglePin,
        setActiveTab,
        setTableOrder,
        setTableHeight,
        resetTableHeight,
        getTableHeight,
        setTableStyle,
        getTableStyle,
        toggleTableReverse,
        isTableReversed,
        setCurrentPage,
        setSearchTerm,
        saveScrollTop,
        enterEditingOrder,
        exitEditingOrder,
        setEditingOrder,
        setMobile,
        setLayout,
        toggleLayout,
        closePanel,
        openPanel,
        setInitialized,
        updateSaveBtnState,
        autoFitPanelHeight,
        saveBallPosition,
        getBallPosition,
        resetBallPosition,
        // Tab 
        setVisibleTabs,
        isTabVisible,
        toggleTabVisibility,
        resetVisibleTabs,
        tabOrder,
        setTabOrder,
        getSortedTabIds,
        moveTab,
        showTab,
        hideTab,
        resetTabConfig,
        syncNewTablesToVisibleTabs,
    };
});
