/**  */
const DEFAULT_BUTTON_ORDER = [
  'save',
  'saveAs',
  'undo',
  'refresh',
  'manualUpdate',
  'purge',
  'pin',
  'toggle',
  'openNative',
  'collapseTab',
  'settings',
];

/**  */
const DEFAULT_BUTTON_GROUPS: ButtonGroup[] = [];

/**  -  DEFAULT_CONFIG */
