/**
 *  composable
 *  useDataStore 
 *
 * @param chatId ID
 * @param tableName 
 * @param row  (TableRow )
 * @param source  ('manual' | 'ai')
 */
export async function saveSnapshot(
  chatId: string,
  tableName: string,
  row: TableRow,
  source: 'manual' | 'ai',
): Promise<void> {
  try {
    const rowKey = makeRowKey(tableName, row.index);

    //  TableRow.cells  Record<number, string>
    const cells: Record<number, string> = {};
    row.cells.forEach((cell, index) => {
      cells[index] = String(cell.value);
    });

    // 
    const existing = await db.getSnapshots(chatId, rowKey);
    const sorted = existing.sort((a, b) => b.timestamp - a.timestamp);

    //  cells 
    if (sorted.length > 0) {
      const latest = sorted[0];
      if (areCellsEqual(cells, latest.cells)) {
        console.info('[ACU History] ');
        return;
      }
    }

    // 
    await db.addSnapshot({
      chatId,
      rowKey,
      timestamp: Date.now(),
      source,
      cells,
    });

    console.info(`[ACU History]  ${rowKey}  (${source})`);

    // 
    if (sorted.length >= MAX_SNAPSHOTS_PER_ROW) {
      const toDelete = sorted.slice(MAX_SNAPSHOTS_PER_ROW - 1);
      for (const snapshot of toDelete) {
        if (snapshot.id !== undefined) {
          await db.deleteSnapshot(snapshot.id);
        }
      }
      console.info(`[ACU History]  ${toDelete.length} `);
    }
  } catch (e) {
    console.error('[ACU History] :', e);
  }
}

