/**
 * IndexedDB 
 *  IndexedDB CRUD 
 *
 * 
 * - 
 * -  chatId 
 * -  500MB 
 */

const DB_NAME = 'acu_history_db';
const DB_VERSION = 1;
const STORE_NAME = 'row_snapshots';

let dbInstance: IDBDatabase | null = null;

/**
 * 
 */
export interface RowSnapshot {
  /** IndexedDB  ID */
  id?: number;
  /**  */
  chatId: string;
  /**  "-" */
  rowKey: string;
  /** Unix  */
  timestamp: number;
  /**  */
  source: 'manual' | 'ai';
  /**  ( -> ) */
  cells: Record<number, string>;
}

/**
 * 
 * 
 */
export async function initDB(): Promise<IDBDatabase> {
  if (dbInstance) return dbInstance;

  return new Promise((resolve, reject) => {
    const request = indexedDB.open(DB_NAME, DB_VERSION);

    request.onerror = () => {
      console.error('[ACU IndexedDB] :', request.error);
      reject(request.error);
    };

    request.onsuccess = () => {
      dbInstance = request.result;
      console.info('[ACU IndexedDB] ');
      resolve(dbInstance);
    };

    request.onupgradeneeded = event => {
      const db = (event.target as IDBOpenDBRequest).result;

      if (!db.objectStoreNames.contains(STORE_NAME)) {
        const store = db.createObjectStore(STORE_NAME, {
          keyPath: 'id',
          autoIncrement: true,
        });

        // +
        store.createIndex('by_chat_row', ['chatId', 'rowKey'], { unique: false });
        // 
        store.createIndex('by_chat', 'chatId', { unique: false });
        // 
        store.createIndex('by_timestamp', 'timestamp', { unique: false });

        console.info('[ACU IndexedDB] ');
      }
    };
  });
}

/**
 * 
 * @param snapshot  id
 * @returns  ID
 */
export async function addSnapshot(snapshot: Omit<RowSnapshot, 'id'>): Promise<number> {
  const db = await initDB();
  return new Promise((resolve, reject) => {
    const tx = db.transaction(STORE_NAME, 'readwrite');
    const store = tx.objectStore(STORE_NAME);
    const request = store.add(snapshot);

    request.onsuccess = () => {
      console.info('[ACU IndexedDB] , ID:', request.result);
      resolve(request.result as number);
    };

    request.onerror = () => {
      console.error('[ACU IndexedDB] :', request.error);
      reject(request.error);
    };
  });
}

/**
 *  chatId + rowKey 
 * @param chatId ID
 * @param rowKey Key
 * @returns 
 */
export async function getSnapshots(chatId: string, rowKey: string): Promise<RowSnapshot[]> {
  const db = await initDB();
  return new Promise((resolve, reject) => {
    const tx = db.transaction(STORE_NAME, 'readonly');
    const store = tx.objectStore(STORE_NAME);
    const index = store.index('by_chat_row');
    const request = index.getAll([chatId, rowKey]);

    request.onsuccess = () => {
      resolve(request.result || []);
    };

    request.onerror = () => {
      console.error('[ACU IndexedDB] :', request.error);
      reject(request.error);
    };
  });
}

/**
 *  ID 
 * @param id ID
 */
export async function deleteSnapshot(id: number): Promise<void> {
  const db = await initDB();
  return new Promise((resolve, reject) => {
    const tx = db.transaction(STORE_NAME, 'readwrite');
    const store = tx.objectStore(STORE_NAME);
    const request = store.delete(id);

    request.onsuccess = () => {
      console.info('[ACU IndexedDB] , ID:', id);
      resolve();
    };

    request.onerror = () => {
      console.error('[ACU IndexedDB] :', request.error);
      reject(request.error);
    };
  });
}

/**
 *  chatId 
 * 
 * @param chatId ID
 */
export async function deleteByChat(chatId: string): Promise<void> {
  const db = await initDB();
  return new Promise((resolve, reject) => {
    const tx = db.transaction(STORE_NAME, 'readwrite');
    const store = tx.objectStore(STORE_NAME);
    const index = store.index('by_chat');
    const request = index.openCursor(IDBKeyRange.only(chatId));

    let deletedCount = 0;

    request.onsuccess = event => {
      const cursor = (event.target as IDBRequest).result;
      if (cursor) {
        cursor.delete();
        deletedCount++;
        cursor.continue();
      }
    };

    tx.oncomplete = () => {
      console.info(`[ACU IndexedDB] ,  ${deletedCount} `);
      resolve();
    };

    tx.onerror = () => {
      console.error('[ACU IndexedDB] :', tx.error);
      reject(tx.error);
    };
  });
}

/**
 * 
 * 
 */
export async function clearAll(): Promise<void> {
  const db = await initDB();
  return new Promise((resolve, reject) => {
    const tx = db.transaction(STORE_NAME, 'readwrite');
    const store = tx.objectStore(STORE_NAME);
    const request = store.clear();

    request.onsuccess = () => {
      console.info('[ACU IndexedDB] ');
      resolve();
    };

    request.onerror = () => {
      console.error('[ACU IndexedDB] :', request.error);
      reject(request.error);
    };
  });
}

/**
 * 
 * 
 */
export async function getStats(): Promise<{ count: number }> {
  const db = await initDB();
  return new Promise((resolve, reject) => {
    const tx = db.transaction(STORE_NAME, 'readonly');
    const store = tx.objectStore(STORE_NAME);
    const request = store.count();

    request.onsuccess = () => {
      resolve({ count: request.result });
    };

    request.onerror = () => {
      reject(request.error);
    };
  });
}
