/**
 *  Composable
 *  AutoCardUpdater 
 *
 * 
 * -  api.getSettings() / api.updateSettings() 
 * -  localStorage  shujuku_*_allSettings_v2
 */

import { ref } from 'vue';
import { getCore } from '../utils';

// ============================================================
// 
// ============================================================

/**  */
export interface DbSettings {
  /** AI */
  autoUpdateThreshold?: number;
  /** N */
  autoUpdateFrequency?: number;
  /**  */
  updateBatchSize?: number;
  /** X */
  skipUpdateFloors?: number;
  /**  */
  [key: string]: unknown;
}

/**  ( API ) */
export interface TableInfo {
  /**  key ( sheet_0) */
  key: string;
  /**  */
  name: string;
  /** / */
  isSummaryOrOutline: boolean;
}

/**  */
export interface PresetExecuteResult {
  success: boolean;
  message: string;
  affectedTables?: string[];
}

// ============================================================
// 
// ============================================================

/**
 *  Key ()
 * @returns localStorage 
 */
function findDatabaseSettingsKey(): string {
  // 
  const direct80 = 'shujuku_v80_allSettings_v2';
  if (localStorage.getItem(direct80)) return direct80;

  const direct70 = 'shujuku_v70_allSettings_v2';
  if (localStorage.getItem(direct70)) return direct70;

  // 
  const keys = Object.keys(localStorage || {});
  const found = keys.find(k => /^shujuku_.*_allSettings_v2$/.test(k));

  return found || direct80;
}

/**
 *  localStorage 
 */
function readFromLocalStorage(): DbSettings {
  try {
    const key = findDatabaseSettingsKey();
    const raw = localStorage.getItem(key);
    return raw ? JSON.parse(raw) : {};
  } catch (e) {
    console.warn('[ACU]  localStorage :', e);
    return {};
  }
}

/**
 *  localStorage
 */
function writeToLocalStorage(settings: DbSettings): void {
  try {
    const key = findDatabaseSettingsKey();
    const existing = readFromLocalStorage();
    localStorage.setItem(key, JSON.stringify({ ...existing, ...settings }));
  } catch (e) {
    console.warn('[ACU]  localStorage :', e);
  }
}

// ============================================================
// Composable
// ============================================================

export function useDbSettings() {
  /**  */
  const settings = ref<DbSettings>({});

  /**  */
  const isLoading = ref(false);

  /**  */
  const isInitialized = ref(false);

  /**
   * 
   *  API localStorage
   */
  async function loadSettings(): Promise<DbSettings> {
    isLoading.value = true;

    try {
      const api = getCore().getDB();

      //  API
      if (api && typeof api.getSettings === 'function') {
        const apiSettings = await api.getSettings();
        if (apiSettings && typeof apiSettings === 'object') {
          settings.value = apiSettings;
          console.info('[ACU]  API ');
          return apiSettings;
        }
      }

      //  localStorage
      const localSettings = readFromLocalStorage();
      settings.value = localSettings;
      console.info('[ACU]  localStorage ');
      return localSettings;
    } catch (e) {
      console.error('[ACU] :', e);
      //  localStorage 
      const localSettings = readFromLocalStorage();
      settings.value = localSettings;
      return localSettings;
    } finally {
      isLoading.value = false;
      isInitialized.value = true;
    }
  }

  /**
   * 
   *  API localStorage
   * @param newSettings 
   */
  async function saveSettings(newSettings: Partial<DbSettings>): Promise<boolean> {
    try {
      const api = getCore().getDB();

      // 
      const mergedSettings = { ...settings.value, ...newSettings };

      //  API
      if (api && typeof api.updateSettings === 'function') {
        await api.updateSettings(mergedSettings);
        settings.value = mergedSettings;
        console.info('[ACU]  API ');
        return true;
      }

      //  localStorage
      writeToLocalStorage(mergedSettings);
      settings.value = mergedSettings;
      console.info('[ACU]  localStorage ');
      return true;
    } catch (e) {
      console.error('[ACU] :', e);
      //  localStorage
      try {
        writeToLocalStorage({ ...settings.value, ...newSettings });
        settings.value = { ...settings.value, ...newSettings };
        return true;
      } catch {
        return false;
      }
    }
  }

  /**
   * 
   * @param key 
   * @param value 
   */
  async function updateSetting<K extends keyof DbSettings>(key: K, value: DbSettings[K]): Promise<void> {
    await saveSettings({ [key]: value } as Partial<DbSettings>);
  }

  /**
   * 
   *  api.manualUpdate() api.triggerUpdate()
   */
  async function executeManualUpdate(): Promise<boolean> {
    try {
      const api = getCore().getDB();

      if (!api) {
        console.warn('[ACU]  API ');
        return false;
      }

      //  manualUpdate
      if (typeof api.manualUpdate === 'function') {
        await api.manualUpdate();
        console.info('[ACU]  (manualUpdate)');
        return true;
      }

      //  triggerUpdate
      if (typeof api.triggerUpdate === 'function') {
        const result = await api.triggerUpdate();
        console.info('[ACU]  (triggerUpdate):', result);
        return !!result;
      }

      console.warn('[ACU] ');
      return false;
    } catch (e) {
      console.error('[ACU] :', e);
      return false;
    }
  }

  /**
   * 
   *  UI
   */
  function getTableList(): TableInfo[] {
    try {
      const api = getCore().getDB();

      if (!api) {
        console.warn('[ACU]  API ');
        return [];
      }

      //  API
      if (typeof api.getTableList === 'function') {
        const list = api.getTableList();
        console.info('[ACU] :', list?.length || 0);
        return list || [];
      }

      //  exportTableAsJson 
      if (typeof api.exportTableAsJson === 'function') {
        const data = api.exportTableAsJson();
        if (data && typeof data === 'object') {
          const tables: TableInfo[] = [];
          Object.keys(data).forEach(key => {
            if (!key.startsWith('sheet_')) return;
            const table = data[key];
            const name = table?.name || key;
            // /
            const isSummaryOrOutline =
              name.includes('') ||
              name.includes('') ||
              name.toLowerCase().includes('summary') ||
              name.toLowerCase().includes('outline');
            tables.push({ key, name, isSummaryOrOutline });
          });
          console.info('[ACU]  exportTableAsJson :', tables.length);
          return tables;
        }
      }

      console.warn('[ACU] ');
      return [];
    } catch (e) {
      console.error('[ACU] :', e);
      return [];
    }
  }

  /**
   *  API
   * 
   *
   * @param presetSettings 
   * @param targetTableNames =
   * @param silent 
   */
  async function executeWithPreset(
    presetSettings: Partial<DbSettings>,
    targetTableNames: string[] = [],
    silent = false,
  ): Promise<PresetExecuteResult> {
    try {
      const api = getCore().getDB();

      if (!api) {
        return { success: false, message: ' API ' };
      }

      //  API: executeWithPreset
      if (typeof api.executeWithPreset === 'function') {
        const result = await api.executeWithPreset({
          settings: {
            autoUpdateThreshold: presetSettings.autoUpdateThreshold,
            updateBatchSize: presetSettings.updateBatchSize,
            skipUpdateFloors: presetSettings.skipUpdateFloors,
            autoUpdateFrequency: presetSettings.autoUpdateFrequency,
          },
          targetTableNames: targetTableNames.length > 0 ? targetTableNames : null,
          silent,
        });
        console.info('[ACU] executeWithPreset :', result);
        return result as PresetExecuteResult;
      }

      // 
      console.info('[ACU] : saveSettings + executeManualUpdate');
      await saveSettings(presetSettings);
      const success = await executeManualUpdate();
      return {
        success,
        message: success ? '' : '',
      };
    } catch (e) {
      console.error('[ACU] :', e);
      return { success: false, message: String(e) };
    }
  }

  return {
    // 
    settings,
    isLoading,
    isInitialized,

    // 
    loadSettings,
    saveSettings,
    updateSetting,
    executeManualUpdate,
    getTableList,
    executeWithPreset,

    // 
    findDatabaseSettingsKey,
  };
}
