/**  */
export type RelationLevel = 'S' | 'A' | 'B' | 'C' | 'D' | 'E' | '?';

/**  */
export interface RelationType {
  /**  */
  level: RelationLevel;
  /**  */
  name: string;
  /**  */
  color: string;
  /**  */
  keywords: string[];
}

/**  */
export interface RelationColorResult {
  /**  */
  color: string;
  /**  */
  level: RelationLevel;
  /**  */
  typeName: string;
  /**  */
  isComplex: boolean;
}

