/**
 *  (jQuery  API)
 */
export function getCore() {
  const w = window.parent || window;
  return {
    $: (window as any).jQuery || (w as any).jQuery,
    getDB: () => (w as any).AutoCardUpdaterAPI || (window as any).AutoCardUpdaterAPI,
  };
}

/**
 *  (API )
 */
export function getTableData(): RawDatabaseData | null {
  const api = getCore().getDB();
  try {
    return api && api.exportTableAsJson ? api.exportTableAsJson() : null;
  } catch (e) {
    console.warn('[ACU] getTableData error:', e);
    return null;
  }
}

/**
 *  JSON 
 * 
 *  name  sheet
 */
export function processJsonData(data: any): Record<string, { key: string; headers: any[]; rows: any[][] }> | null {
  if (!data || typeof data !== 'object') return {};
  const tables: Record<string, { key: string; headers: any[]; rows: any[][] }> = {};
  for (const sheetId in data) {
    //  name  sheet
    if (data[sheetId]?.name) {
      const sheet = data[sheetId];
      tables[sheet.name] = {
        key: sheetId, //  Key 
        headers: sheet.content?.[0] || [],
        rows: sheet.content?.slice(1) || [],
      };
    }
  }
  //  null
  return Object.keys(tables).length > 0 ? tables : null;
}

/**
 * 
 */
export function getIconForTableName(name: string): string {
  if (name.includes('')) return 'fa-tachometer-alt';
  if (name.includes('')) return 'fa-list-check';
  if (name.includes('') || name.includes('')) return 'fa-user-circle';
  if (name.includes('') || name.includes('') || name.includes('NPC')) return 'fa-users';
  if (name.includes('') || name.includes('')) return 'fa-tasks';
  if (name.includes('') || name.includes('') || name.includes('')) return 'fa-box-open';
  if (name.includes('') || name.includes('')) return 'fa-bolt';
  if (name.includes('') || name.includes('')) return 'fa-map-marked-alt';
  if (name.includes('') || name.includes('')) return 'fa-book';
  return 'fa-table';
}

/**
 * 
 *  class
 */
export function getBadgeStyle(text: string): string {
  if (!text) return '';
  const str = String(text);
  if (str === 'True' || str === 'Yes' || str === '' || str === '') return 'acu-badge-success';
  if (str === 'False' || str === 'No' || str === '' || str === '') return 'acu-badge-danger';
  if (str === '' || str === 'Doing') return 'acu-badge-warning';
  if (/^\d+%$/.test(str)) return 'acu-badge-info'; // 
  if (/^Lv\.?\d+$/i.test(str)) return 'acu-badge-primary'; // 
  return '';
}
